import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { Login1Component } from './login1/login1.component';
import { Login2Component } from './login2/login2.component';
import { Login3Component } from './login3/login3.component';
import { Login4Component } from './login4/login4.component';
import { Login5Component } from './login5/login5.component';
import { PostComponent } from './post/post.component';
import { SubpostComponent } from './subpost/subpost.component';
import { LoginComponent } from './login/login.component';


const routes:Routes = [
  {path:'login1',component:Login1Component},
  {path:'login2',component:Login2Component},
  {path:'login3',component:Login3Component},
  {path:'login4',component:Login4Component},
  {path:'login5',component:Login5Component},
  {path:'post',component:PostComponent},
  {path:'post/:id',component:SubpostComponent},
  {path:'userlogin',component:LoginComponent},
];
@NgModule({
  imports : [RouterModule.forRoot(routes)],
  exports: [ RouterModule ]
})
export class AppRoutingModule { }

export const routingComponents = [Login1Component,Login2Component,Login3Component,Login4Component,Login5Component,PostComponent,SubpostComponent,LoginComponent];
